<template>
  <div class="list-view-container">
    <div class="header">
      <div class="inner-wrapper">
        <div class="head-nav">
          <div class="header-list" :class="{'active': activeTab == 'my'}" @click="navTo('my')">
            我的项目
            <div class="underLine" :class="{'active': activeTab == 'my'}"></div>
          </div>
          <div class="header-list" :class="{'active': activeTab == 'template'}" @click="navTo('template')">
            已授权模板
            <div class="underLine" :class="{'active': activeTab == 'template'}"></div>
          </div>
        </div>
        <div class="user">
          <span class="username">{{name}}</span>
          <i class="el-icon-switch-button" @click="logout"></i>
        </div>
      </div>
    </div>
    <!-- <list :path="activeTab"></list> -->
    <router-view :key="$route.fullPath"></router-view>
  </div>
</template>

<script>
  import api from '~/api/api';
  import list from '~/components/list';
  import {getStore} from '~/tools/utils';

  export default {
    data() {
      return {
        activeTab: 'my',
        name: getStore('name')
      };
    },
    mounted() {
      this.navTo('my');
    },
    components: {
      list
    },
    methods: {
      logout() {
        api.logout().then(res => {
          this.$router.push({ path: `/login` });
        })
      },
      navTo(path) {
        this.activeTab = path;
        this.$router.push({ path: `/list/${path}` });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .list-view-container {
    min-height: 100%;
    width: 100%;
    background #EEEFF1
    overflow: hidden;
    .header {
      position: static;
      -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
      box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
      background: #fff;
      font-size: 16px;
      -webkit-transition: all .4s ease;
      -o-transition: all .4s ease;
      transition: all .4s ease;
      height: 64px;
      .inner-wrapper {
        min-width 1306px
        padding 0 40px
        margin 0 auto
        .head-nav {
          padding-top: 22px;
          .header-list {
            float: left;
            margin-right: 24px;
            font-size: 15px;
            padding-bottom: 16px;
            line-height: 24px;
            box-sizing border-box
            position relative
            &.active {
              color: #2672FF;
              border-bottom: 2px solid #2672FF
            }
          }
        }
        .user {
          float right
          .username {
            margin-right 10px
          }
          i {
            &:hover {
              color red
            }
          }
        }
      }
    }
  }
</style>



